<template lang="html">
  <div class="about-us-page">
    <!-- 头部 -->
    <default-head v-if="hasHead" header="关于我们"></default-head>
    <div class="logo">
      <img src="../assets/img/logo.png" alt="">
    </div>

    <div class="text-area">
      <div class="text-input">
        <textarea disabled="true" resize="none" class="">
          “号召力”APP隶属于广州大手互联网科技有限公司，是一款卖家通过上传标签信息，让买家直接搜索到卖家联系方式的号码搜索引擎。“号召力”重新定义了人的价值，只要能提供服务即为商家，真正把人的价值深度挖掘并共享出来。“号召力”APP通过用户上传个人标签，用户与用户之间互相服务，不仅解决了商业双向性的问题，也解决了传统商业模式中企业服务个人的单一性，增加了个人对个人，个人对企业，企业对企业，企业对个人等多种模式，号召力APP创造性把普通的“物品共享”模式提升到了“人的价值共享”模式。
        </textarea>
      </div>
    </div>

    <p class="contact borderB">联系我们 400-000-7246</p>
    <p class="contact" v-if="false">关注我们</p>
  </div>
</template>

<script>
import api from '@/api'
import defaultHead from '@/components/head/defaultHead'
export default {
  data () {
    return {
      content: '',
      hasHead: true
    }
  },
  components: { defaultHead },
  mounted () {
    this.isAndroid()
  },
  methods: {
    ajaxAboutUsList (data) {
      api.aboutUsList(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.content = result.data.aboutUs.content
        }
      })
    },
    isAndroid () {
      var isAndroid = window.navigator.appVersion.match(/android/gi)
      if (isAndroid) {
        this.hasHead = false
        document.querySelector('.about-us-page').style.paddingTop = 0
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.about-us-page {
  padding-top: .88rem;
  .logo {
    height: 1.8rem;
    padding: .3rem 0;
    text-align: center;
    background: @whiteColor;
    img {
      width: 1.2rem;
    }
  }
  .text-area {
    background: @whiteColor;
  }
  .text-input {
    margin: 0 .22rem;
    padding-bottom: .15rem;
    textarea {
      width: 100%;
      height: 7.4rem;
      font-size: .32rem;
      padding: 0 .2rem;
      color: @defaultColor;
      border-color: @lineColor;
      border-radius: .08rem;
      box-shadow:0px 0px 0px rgba(0,0,0,0);
     -webkit-appearance:none;
      &:disabled {
        color: @defaultColor;
        background: @whiteColor;
        -webkit-opacity:1;
        opacity: 1;
      }
    }
  }
  .contact {
    font-size: .32rem;
    color: @defaultColor;
    height: .88rem;
    line-height: .88rem;
    text-align: center;
    background: @whiteColor;
    &:nth-of-type(1) {
      margin-top: .3rem;
    }
  }
}

</style>



